<template>
  <div id="adult" class="online">
    <Banner msg="Online education" msm=""></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="adult-con">
      <div class="adult-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,its)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list  animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p v-for="its in item.con">{{its}}</p>
              <p>
                <strong></strong>
                {{item.end}}
              </p>
              <div class="pic">
                <img :src="item.pic1">
                <img :src="item.pic2">
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from "../../components/banner"
  import Nav from "../../components/nav"
  export default {
    components: {
      Banner, Nav,
    },
    data() {
      return {
        nav: " >  > >",
        nav2: "",
        nowindex:[0,0],
        listL:[
          {
            title:"",
            spanAC1:false,
            spanAC2:true,
            fen:["","","",""],
            pActive:true,
          }
        ],
        listR:[
          {
            con:[
              {
                title:"",
                con:[
                  "Ningbo Institute of Technology,Zhejiang University2001 6 ",
                  "1200369,37 31.11000"
                ],
                end:" ",
                pic1:require("../../assets/images/online/adult1-1.jpg"),
                pic2:require("../../assets/images/online/adult1-2.jpg"),
              },
              {
                title:"",
                con:[
                  "Lishui University1907 20045 ",
                  "1008.1638.74745 2412607 10681"
                ],
                end:"       ",
                pic1:require("../../assets/images/online/adult2-1.jpg"),
                pic2:require("../../assets/images/online/adult2-2.jpg"),
              },
              {
                title:"",
                con:[
                  "Ningbo Polytechnic, 2006",
                  "1999 1984900016000"
                ],
                end:" ",
                pic1:require("../../assets/images/online/adult3-1.jpg"),
                pic2:require("../../assets/images/online/adult3-2.jpg"),
              },
              {
                title:"",
                con:[
                  "20033 1992 2001",
                ],
                end:" ",
                pic1:require("../../assets/images/online/adult4-1.jpg"),
              }
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style></style>



