<template>
  <div id="about">
    <Banner msg="ABOUT RENREN" msm=""></Banner>
    <div class="about-one">
      <div class="one-center">
        <h3 class="animated slideInLeft"></h3>
        <div class="xin animated slideInLeft" >
          <p></p>
          <p>Care for all who are eager to learn</p>
        </div>
        <img class="animated slideInRight" src="../assets/images/about/about_part01_pic.png">
      </div>
    </div>
    <div class="about-con">
      <div class="about-pro">
        <div class="pro-one">
          <Title msg="/" msm="School profile"></Title>
          <p class="p-name wow slideInLeft">1999</p>
          <p class="p-name wow slideInLeft">MPAMBASE</p>
          <p class="p-name wow slideInRight"></p>
          <p class="p-name wow slideInRight">20</p>
        </div>
        <div class="pro-two">
          <dl class="wow" v-for="item in pro2" :class="item.class">
            <dt><img :src="item.pic"></dt>
            <dd>
              <h4>{{item.h4}}</h4>
              <p>{{item.p}}</p>
            </dd>
          </dl>
          <div class="about-line wow slideInRight"></div>
        </div>
      </div>
      <div class="about-hon">
        <div class="hon-one">
          <Title msg="/" msm="Honorary certificate"></Title>
          <div class="hon-ti wow slideInLeft">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
          <img class="wow slideInRight" src="../assets/images/about/abou_part03_pic01.png">
        </div>
      </div>
      <Coo></Coo>
    </div>
  </div>
</template>

<script>
  import Fix from "../components/fix"
  import Banner from "../components/banner"
  import Title from "../components/title"
  import Coo from "../components/Coo"
  import {WOW} from '../assets/js/wow.min.js'
  export default {
    components: {
      Fix,Banner,Title,Coo
    },
    data(){
      return{
        pro2:[
          {
            pic:require("../assets/images/about/about_part02_pic01.png"),
            h4:"",
            p:"1999",
            class:"slideInLeft"
          },
          {
            pic:require("../assets/images/about/about_part02_pic02.png"),
            h4:"",
            p:"20...",
            class:"slideInUp"
          },
          {
            pic:require("../assets/images/about/about_part02_pic03.png"),
            h4:"",
            p:"...",
            class:"slideInRight"
          }
        ]
      }
    },
    methods:{
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    },
    mounted(){
      new WOW().init();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #about{
    width:100%;
  }
</style>



