<template>
  <div id="onjob" class="online">
    <Banner msg="Online education" msm=""></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="onjob-con">
      <div class="onjob-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,item)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list  animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p v-for="its in item.con1">{{its}}</p>
              <h3>{{item.hcc}}</h3>
              <p v-for="itm in item.con2">{{itm}}</p>
              <h3>{{item.hmc}}</h3>
              <p v-for="itp in item.con3">{{itp}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from "../../components/banner"
  import Nav from "../../components/nav"
  export default {
    components: {
      Banner, Nav,
    },
    data() {
      return {
        nav: " >  > >",
        nav2: ">",
        nowindex:[0,0],
        listL:[
          {
            title:"",
            spanAC1:false,
            spanAC2:true,
            fen:["","","",""],
            pActive:true,
          },
          {
            title:"",
            spanAC1:true,
            spanAC2:false,
            fen:["","",],
            pActive:false,
          }
        ],
        listR:[
          {
            con:[
              {
                title:"",
                con1:[
                  "10488    861 9 49415"
                ],
                hcc:"MBA",
                con2:[
                  "3",
                  "",
                  "5.4"
                ]
              },
              {
                title:"",
                con1:[
                  "1. 320159",
                  "2. 520139",
                  "3. "
                ],
                hcc:"",
                con2:[
                  "1. 20177~120574-2782666913867450832 2. 201710http://yz.chsi.cn",
                  "2. 201710http://yz.chsi.cn",
                  "3. 201711 ",
                  "4. 12 ",
                  "5.  ",
                  "6. "
                ]
              },
              {
                title:"",
                con1:[
                  "1. ",
                  "2. "
                ],
                hcc:"",
                con2:[
                  "50%5",
                  "2017 [2017]40",
                ]
              },
              {
                title:"",
                con1:[
                  "1. 19",
                  "2. 84.6%"
                ],
                hcc:"",
                con2:[
                  "1. +",
                  "2. ++=312",
                  "3. 9",
                  "4. 1480010000"
                ],
                hmc:"",
                con3:[
                  "1.199",
                  "2.204"
                ]
              },
            ]
          },
          {
            con:[
              {
                title:"",
                con1:[
                  "1974198720062013",
                ],
                hcc:"",
                con2:[
                  "2017111492108016234554211121212131512233141211057710014"
                ],
              },
              {
                title:"",
                con1:[
                  "68421700",

                ],
                hcc:"MBA",
                con2:["\"MBA"]
              }
            ],
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con.title+">"+con.fen[b];
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

