<template>
  <div id="home">
    <!--banner-->
    <div class="banner">
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide><img src="../assets/images/home/index_banner.png"></swiper-slide>
        <swiper-slide><img src="../assets/images/home/index_banner2.png"></swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination "  slot="pagination">
          <span class="swiper-pagination-bullet"></span>
          <span class="swiper-pagination-bullet"></span>
        </div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
      <div class="banner-font">
        <div class="font-bg"></div>
        <Title msg="" msg2=""></Title>
        <p class="more" @click="more"></p>
        <ul class="news-con">
          <li v-for="(item , i) in list1" :class="{show:i==nowindex}">
            <h4>{{item.h4}}</h4>
            <p>{{item.h4p}}</p>
            <div class="line"></div>
            <p>{{item.p1}}</p>
            <p>{{item.p2}}</p>
            <p>{{item.p3}}</p>
            <p>{{item.p4}}</p>
            <p>{{item.p5}}</p>
          </li>
        </ul>
        <ol>
          <li v-for="(item , m ) in list2" :class="{active:m==nowindex}" @mouseover="change(m)"></li>
        </ol>
      </div>
    </div>
    <!--banner-->
    <div class="con">
      <!---->
      <div class="home-one">
        <div class="left-con animated slideInLeft">
          <Title msg="/" msm="Online education"></Title>
          <dl v-for="(item , i) in online">
            <dt @click="goOnline(item.url)"><img :src="item.pic"></dt>
            <dd>
              <h5>{{item.h5}}</h5>
              <p>{{item.p}}</p>
            </dd>
          </dl>
        </div>
        <div class="right-con animated slideInRight">
          <div class="public">
            <Title msg="/" msm="Public service"></Title>
            <img src="../assets/images/home/index_map.png">
            <p class="pub-font">....</p>
            <p class="more">more<span></span></p>
          </div>
          <div class="tec">
            <Title msg="/" msm="Technology"></Title>
            <p class="pub-font"></p>
            <ul>
              <li v-for="(item ,i) in tec" :class="{active:i==tecnow}" @mouseover="tecchange(i)">{{item}}</li>
            </ul>
          </div>
          <div class="tec">
            <Title msg="/" msm="Technology"></Title>
            <p class="pub-font"></p>
            <ul>
              <li v-for="(item ,i) in tec2" :class="{active:i==tecnow2}" @mouseover="tecchange2(i)">{{item}}</li>
            </ul>
          </div>
        </div>
      </div>
      <!---->
      <!---->
      <div class="home-two">
        <Title msg="/" msm="Public course"></Title>
        <More msg="/netedu"></More>
        <ul>
          <li class="wow slideInLeft">
            <h3></h3>
            <p>...</p>
          </li>
          <li class="wow slideInRight">
            <h3></h3>
            <p>...</p>
          </li>
          <li class="wow slideInUp">
            <h3></h3>
            <p>...</p>
          </li>
          <li class="wow slideInUp">
            <h3></h3>
            <p>...</p>
          </li>
        </ul>
      </div>
      <!---->
      <!---->
      <div class="home-three">
        <div class="three-top">
          <p></p>
          <h2></h2>
          <p></p>
        </div>
        <h3>Education project</h3>
        <div class="pro-con">
          <dl class="wow" v-for="item in pro" :class="[item.class]">
            <dt><img :src="item.pic"></dt>
            <dd>
              <h4>{{item.title}}</h4>
              <p>{{item.con}}</p>
              <p class="dian"></p>
            </dd>
          </dl>
        </div>
      </div>
      <!---->
      <Coo></Coo>
    </div>
  </div>
</template>

<script>
  import "../assets/css/swiper-3.4.2.min.css"
  import {WOW} from '../assets/js/wow.min.js'
  import Title from "../components/title"
  import Coo from "../components/Coo"
  import Fix from "../components/fix"
  import More from "../components/more"
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    components: {
      swiper,
      swiperSlide,
      Title,
      Coo,
      Fix,
      More
    },
    data() {
      return {
        nowindex:0,
        tecnow:0,
        tecnow2:0,
        list1:[
          {
            h4:"",
            h4p:"()...",
            p1:"    2016",
            p2:"  ",
            p3:"   ",
            p4:"  ",
            p5:"  2017",
          },
          {
            h4:"",
            h4p:"()...",
            p1:"    20",
            p2:"  ",
            p3:"   ",
            p4:"  ",
            p5:"  2017",
          }
        ],
        list2:["",""],
        swiperOption: {
          // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
          // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
          notNextTick: true,
          // swiper configs swiperapi
          autoplay: {
            delay: 3500,
            stopOnLastSlide: false,
            disableOnInteraction: true,
          },
          loop:true,
          loopAdditionalSlides:0,
          // direction : 'vertical',
          effect: "slide",
          navigation: {
            //
            nextEl: '.swiper-button-next',
          },
          pagination: {
            el: '.swiper-pagination',
          },
          grabCursor: true,
          setWrapperSize: true,
          // autoHeight: true,
          // paginationType:"bullets",
          paginationClickable: true,
          prevButton: '.swiper-button-prev',
          nextButton: '.swiper-button-next',
          // scrollbar:'.swiper-scrollbar',
          mousewheelControl: true,
          observeParents: true,
        },
        online:[
          {
            pic:require("../assets/images/home/index_pic01.png"),
            h5:"",
            url:"/adult",
            p:"16..."
          },
          {
            pic:require("../assets/images/home/index_pic02.png"),
            h5:"",
            url:"/netedu",
            p:"..."
          },
          {
            pic:require("../assets/images/home/index_pic03.png"),
            h5:"",
            url:"/learnself",
            p:"MOOC"
          },
          {
            pic:require("../assets/images/home/index_pic04.png"),
            h5:"",
            url:"/onjob",
            p:"..."
          }
        ],
        tec:["","","(LMS)","","",""],
        tec2:["",""],
        pro:[
          {
            pic:require("../assets/images/home/index_project_pic01.png"),
            title:"",
            con:"...",
            class:"slideInLeft"
          },
          {
            pic:require("../assets/images/home/index_project_pic02.png"),
            title:"",
            con:"...",
            class:"slideInUp"
          },
          {
            pic:require("../assets/images/home/index_project_pic03.png"),
            title:"",
            con:"...",
            class:"slideInRight"
          }
        ],

      }
    },
    methods:{
      more(){
        this.$router.push({path:'/news'})
      },
      change(index){
        this.nowindex=index;
      },
      goOnline(url){
        this.$router.push({path:url});
      },
      tecchange(index){
        this.tecnow=index;
      },
      tecchange2(index){
        this.tecnow2=index;
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    created(){
      this.menu();
    },
    mounted(){
      new WOW().init();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #home{
    width:100%;
  }
</style>



