<template>
  <div id="eduintro">
    <Banner msg="entrance guide" msm=""></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="ed-con">
      <div class="ed-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,its)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p>{{item.top}}</p>
              <h4>{{item.h4}}</h4>
              <p v-for="its in item.con">
                {{its.ding}}
                <span>{{its.ju1}}</span>
                <span>{{its.ju2}}</span>
                <span>{{its.ju3}}</span>
                <span>{{its.ju4}}</span>
                <span>{{its.ju5}}</span>
                <span>{{its.ju6}}</span>
              </p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Fix from "../components/fix"
  import Banner from "../components/banner"
  import Nav from "../components/nav"
  export default {
    components:{
      Banner,Nav,Fix
    },
    data(){
      return {
        nav: " >  > ",
        nav2:" ",
        nowindex:[0,0],
        listL:[
          {
            title:"",
            spanAC1:false,
            spanAC2:true,
            fen:["","",""],
            pActive:true,
          },
        ],
        listR:[
          {
            con:[
              {
                title:"",
                top:"",
                h4:"1.",
                show:true,
                con:[
                  {
                    ding:"1.1",
                  },
                  {
                    ding:"1.2",
                    ju1:"http://www.chsi.com.cnhttp://www.crhsi.com.cn/"
                  },
                  {
                    ding:"1.3",
                  },
                  {
                    ding:"1.3.1",
                    ju1:"",
                    ju2:"",
                    ju3:"3",
                    ju4:"",
                    ju5:"",
                    ju6:""
                  }
                ]
              },
              {
                title:"",
                top:"",
                h4:"2.",
                show:false,
                con:[
                  {
                    ding:"2.1 ",
                  },
                  {
                    ding:"2.2",
                  },
                  {
                    ding:"2.31820",
                  },
                  {
                    ding:"2.41.522.5333",
                  },
                  {
                    ding:""
                  }
                ]
              },
              {
                title:"",
                top:"",
                h4:"3.",
                show:false,
                con:[
                  {
                    ding:"3.1"
                  },
                  {
                    ding:"3.2",
                  },
                  {
                    ding:"3.3",
                  },
                  {
                    ding:"3.4",
                  },
                  {
                    ding:"3.5"
                  },
                  {
                    ding:"3.6"
                  },
                ]
              }
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
         return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #eduintro{
    width:100%;
  }
</style>



